import { LinkContainer } from "react-router-bootstrap";
import { Button, Card } from "react-bootstrap";
import { BsCardImage } from "react-icons/bs";

export default function SLOCard(props) {
  return (
    <div style={{ borderRadius: "50%" }}>
      <LinkContainer
        to={`${props.linkTo}`}
        style={{
          textDecoration: "none",
          color: "red",
          borderRadius: "1em",
          textAlign: "left",
          boxShadow: "0 5px 10px rgba(0,0,0,.2)",
          backgroundImage: `url(${props.cardImageSrc})`,
          backgroundSize: "cover",
          backgroundPosition: "center"
        }}
      >
        <Card
          as="a"
          className="shadow mb-3 text-center "
          style={{
            borderRadius: "50%",
            textAlign: "left",
            boxShadow: "0 5px 10px rgba(0,0,0,.2)"
          }}
        >
          <Card.Body>
            <Card.Title
              style={{
                fontSize: `${props.propsFontSize}`,
                textAlign: "left",
                marginTop: "30%"
              }}
            >
              {props.cardTitle}
            </Card.Title>
            <Card.Text>{props.cardText}</Card.Text>
          </Card.Body>{" "}
        </Card>
      </LinkContainer>
    </div>
  );
}
