import SLOCard from "../../components/SLOCard";
import { Container, Row, Col, Card } from "react-bootstrap";

import { useParams } from "react-router-dom";

import * as textHome from "../../Text/home";
import LanguageCard from "../../components/LanguageCard";

export default function Home() {
  let { lng } = useParams();

  return (
    <div>
      <div class="hero-background-home">
        <Card style={{ padding: "3%" }}>
          <Card.Title>Scottish Lavender Oils</Card.Title>
          <Card.Text>
            <b>{textHome.textHome[lng].welcome}!</b>
          </Card.Text>
        </Card>
      </div>

      <Container>
        <Row style={{ marginTop: "5%" }}>
          <SLOCard
            cardTitle="Tour"
            linkTo={`/${lng}/tour`}
            cardText=""
            cardImageSrc="https://www.dutchfarmexperience.com/wp-content/uploads/2020/07/dutch-farm-experience-tailor-made-farm-tours-and-workshops.jpg"
            propsFontSize="18px"
          />

          <Col lg="12" md="12" sm="12" xs="12">
            <SLOCard
              cardTitle="Plants"
              linkTo={`/${lng}/plants`}
              cardText=""
              cardImageSrc="https://www.gardendesign.com/pictures/images/900x705Max/site_3/english-lavender-lavandula-angustifolia-garden-design_11716.jpg"
              propsFontSize="18px"
            />
          </Col>

          <Col lg="12" md="12" sm="12" xs="12">
            <SLOCard
              cardTitle="Site Map"
              linkTo={`/${lng}/map`}
              cardText=""
              cardImageSrc="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT6hSZUsCgOVh-3upjjuMT-S5IryZy_7ZJtEQ"
              propsFontSize="18px"
            />
          </Col>

          <Col lg="3" md="4" sm="6" xs="6">
            <LanguageCard
              cardTitle="English"
              linkTo={`/en/home`}
              cardText=""
              cardImageSrc="https://upload.wikimedia.org/wikipedia/commons/thumb/f/f2/Flag_of_Great_Britain_%281707%E2%80%931800%29.svg/1200px-Flag_of_Great_Britain_%281707%E2%80%931800%29.svg.png"
              propsFontSize="18px"
            />
          </Col>
          <Col lg="3" md="4" sm="6" xs="6">
            <LanguageCard
              cardTitle="Polish"
              linkTo={`/pl/home`}
              cardText=""
              cardImageSrc="https://upload.wikimedia.org/wikipedia/en/thumb/1/12/Flag_of_Poland.svg/1200px-Flag_of_Poland.svg.png"
              propsFontSize="18px"
            />
          </Col>
          <Col lg="3" md="4" sm="6" xs="6">
            <LanguageCard
              cardTitle="Mandarin"
              linkTo={`/cn/home`}
              cardText=""
              cardImageSrc="https://cdn.britannica.com/90/7490-004-BAD4AA72/Flag-China.jpg"
              propsFontSize="18px"
            />
          </Col>
          <Col lg="3" md="4" sm="6" xs="6">
            <LanguageCard
              cardTitle="Hindi"
              linkTo={`/in/home`}
              cardText=""
              cardImageSrc="https://upload.wikimedia.org/wikipedia/en/thumb/4/41/Flag_of_India.svg/1200px-Flag_of_India.svg.png"
              propsFontSize="18px"
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
}
