import { Button, Card, Container, Row, Col } from "react-bootstrap";

import PlantsCard from "../../components/PlantsCard";
import ReturnButton from "../../components/ReturnButton";
import ReturnBanner from "../../components/ReturnBanner";
import { useParams } from "react-router-dom";
import { LinkContainer } from "react-router-bootstrap";
import { PlantList } from "../../data/plants/list";
import { PlantTranslate } from "../../data/plants/translate";

export default function Plants() {
  let { lng } = useParams();
  let Lst = Object.values(PlantList);
  return (
    <>
      <ReturnBanner returnTo={`/${lng}/home`} title="Plants" />
      {Lst.map((p) => (
        <div>
          <LinkContainer to={`/${lng}/plants/${p}`}>
            <Button>{PlantTranslate[lng][p]}</Button>
          </LinkContainer>
        </div>
      ))}
    </>
  );
}
