import { Button, Card, Container, Row, Col } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import GetData from "../GetData";

export default function PlantsCard(props) {
  return (
    <Col md="6" sm="6">
      <Card
        style={{
          "text-align": "center",
          margin: "10px",
          "min-height": "320px"
        }}
      >
        <Card.Img
          style={{ "object-fit": "cover", "max-height": "175px" }}
          src={GetData([props.plant]).plantURL}
          variant="top"
        />
        <Card.Body>
          <Card.Title>{GetData([props.plant]).plantName}</Card.Title>
          <Card.Text>{GetData([props.plant]).plantDescription}</Card.Text>
          <div>
            <LinkContainer to={`/plants/${props.plant}`}>
              <Button className="w-100 custom-btn" variant="primary">
                Learn More
              </Button>
            </LinkContainer>
          </div>
        </Card.Body>
      </Card>
    </Col>
  );
}
