import { LinkContainer } from "react-router-bootstrap";
import { Button } from "react-bootstrap";
import { BiChevronLeft } from "react-icons/bi";

export default function ReturnButton(props) {
  return (
    <LinkContainer
      to={props.returnTo}
      style={{
        backgroundColor: "white",
        margin: "10px",
        width: "40px",
        height: "40px",
        borderRadius: "50%"
      }}
    >
      <BiChevronLeft />
    </LinkContainer>
  );
}
