import { Button, Col, Row } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useParams } from "react-router-dom";
import ReturnBanner from "../../components/ReturnBanner";
import VideoSection from "../PlantPage/components/videoSection";

export default function TourPage() {
  let { lng, tournumber } = useParams();
  let hasVideo = true;
  return (
    <>
      <ReturnBanner
        returnTo={`/${lng}/tour`}
        title={`title of the slide ${tournumber}`}
      />
      <div style={{ backgroundColor: "green", padding: "15%" }}></div>
      <div>
        <p>
          text fkjosdijfoidsjfo ijdfs oij fiosd jfioj dsoif odsi jfjio doi
          foisdjfio djsf oidsfiojdjsf oidjs
        </p>
      </div>
      {hasVideo ? (
        <div>
          <VideoSection plantname={"lavender"} />
        </div>
      ) : null}

      <Row>
        <Col>
          <LinkContainer to={`/${lng}/tour/${parseInt(tournumber) - 1}`}>
            <Button>Back</Button>
          </LinkContainer>
        </Col>
        <Col>
          <LinkContainer to={`/${lng}/tour/${parseInt(tournumber) + 1}`}>
            <Button>Next</Button>
          </LinkContainer>
        </Col>
      </Row>
    </>
  );
}
