import { Navbar, Container, NavLink, Nav, NavDropdown } from "react-bootstrap";
import { Link, useParams, useLocation } from "react-router-dom";
import { useState } from "react";

import { matchPath } from "react-router";

function SLONavbar() {
  const [expanded, setExpanded] = useState(false);

  const { pathname } = useLocation();
  let lng = pathname.split("/")[1];

  return (
    <Navbar
      variant="light"
      className="SLONavBar"
      expand="lg"
      staticTop="true"
      expanded={expanded}
      fixed="top"
      style={{
        "background-color": "white",
        position: "sticky",
        top: 0
      }}
    >
      <Container>
        <Navbar.Brand as={Link} to={`/${lng}`}>
          <div>
            <img
              src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/Scottish_Lavender_Oils_Website_Header_500x.png?v=1635342911"
              height="50"
              alt="Scottish Lavender Oils Logo"
            />
          </div>
        </Navbar.Brand>
        <Navbar.Toggle
          aria-controls="basic-navbar-nav"
          onClick={() => setExpanded(expanded ? false : "expanded")}
        />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to={`/${lng}`}
            >
              Home
            </Nav.Link>
            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to={`/${lng}/tours`}
            >
              Tours
            </Nav.Link>
            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to={`/${lng}/map`}
            >
              Site Map
            </Nav.Link>
            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to={`/${lng}/plants`}
            >
              Plants
            </Nav.Link>
            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to={`/${lng}/process`}
            >
              Our Process
            </Nav.Link>

            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to={`/${lng}/products`}
            >
              Our Products
            </Nav.Link>
            <Nav.Link onClick={() => setExpanded(false)} as={Link} to={`/en`}>
              English
            </Nav.Link>
            <Nav.Link onClick={() => setExpanded(false)} as={Link} to={`/pl`}>
              Polish
            </Nav.Link>

            <Nav.Link onClick={() => setExpanded(false)} as={Link} to={`/cn`}>
              Chinese
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default SLONavbar;
