import { useParams } from "react-router-dom";
import GetData from "../../components/GetData/index";
import ReturnButton from "../../components/ReturnButton";
import BlendsWellWith from "../../components/BlendsWellWith";
import BlendsWellWithCard from "../../components/BlendsWellWithCard";
import { Button, Row } from "react-bootstrap";

import * as productsContainOilData from "../../data/productsContainOil/productsContainOilData";
import ProductCard from "../../components/ProductCard";
import ButtonPlayVideo from "../../components/ButtonPlayVideo/ButtonPlayVideo";
import ImageHeader from "./components/imageHeader";
import VideoSection from "./components/videoSection";
import ReturnBanner from "../../components/ReturnBanner";
import React, { useEffect, useState } from "react";

const axios = require("axios");

export default function PlantPage() {
  const { lng, plantname } = useParams();

  const [fetchedData, setFetchedData] = useState([]);
  useEffect(() => {
    const getData = async () => {
      const data = await axios.get(
        "https://jsonplaceholder.typicode.com/todos/1"
      );
      setFetchedData(data);
    };
    getData();
  }, []);

  console.log("data: ", fetchedData);

  let x = <p>testtt</p>;

  return (
    <div style={{ "background-color": "#F9F9F9" }}>
      <ReturnBanner title="lav" returnTo={`/${lng}/plants`} />

      <div style={{ textAlign: "center" }}>
        {/* back to plants button */}

        {/* Header image */}

        <ImageHeader plantname={plantname} />

        <hr />

        {x}

        <hr />

        {/* Plant Video */}

        <VideoSection plantname={plantname} />

        {/* Plant description */}
        <div style={{ margin: "5%" }}>
          {" "}
          <p>{GetData([plantname]).plantDescriptionLong}</p>
        </div>
      </div>
    </div>
  );
}
